html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}


/*импорт переменных*/
@font-face {
  font-family: Playfair;
  src: url('../../fonts/PlayfairDisplay-Black.ttf') format('ttf'), url('../../fonts/PlayfairDisplay-Black.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: Playfair;
  src: url('../../fonts/PlayfairDisplay-Bold.ttf') format('ttf'), url('../../fonts/PlayfairDisplay-Bold.woff') format('woff');
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: Playfair;
  src: url('../../fonts/PlayfairDisplay-Regular.ttf') format('ttf'), url('../../fonts/PlayfairDisplay-Regular.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: Playfair;
  src: url('../../fonts/HammersmithOne-Regular.ttf') format('ttf'), url('../../fonts/HammersmithOne-Regular.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

